
function startTime() {
    var today = new Date();
    var h = today.getHours();
    var m = today.getMinutes();
    var s = today.getSeconds();
    var dt = today.getDate();
    var date=["<sup>st</sup>","<sup>nd</sup>","<sup>rd</sup>","<sup>th</sup>"];
    var d;
    var dy = today.getDay();
    var mo = today.getMonth();
    var y = today.getFullYear();
    var g;
    m = checkTime(m);
    s = checkTime(s);
    document.fgColor="#f2f2f2"
    var t = setTimeout(startTime, 500);
    switch(dy){
        case 0: dy="Sunday";break;
        case 1: dy="Monday";break;
        case 2: dy="Tuesday";break;
        case 3: dy="Wednesday";break;
        case 4: dy="Thursday";break;
        case 5: dy="Friday";break;
        case 6: dy="Saturday";break;
    }
    switch(dt){
        case 01: d=dt+date[0];break;
        case 02: d=dt+date[1];break;
        case 03: d=dt+date[2];break;
        default: d=dt+date[3];break;
    }
    switch(mo){
        case 0: mo="January";break;
        case 1: mo="February";break;
        case 2: mo="March";break;
        case 3: mo="April";break;
        case 4: mo="May";break;
        case 5: mo="June";break;
        case 6: mo="July";break;
        case 7: mo="Augusrt";break;
        case 8: mo="September";break;
        case 9: mo="October";break;
        case 10: mo="November";break;
        case 11: mo="December";break;
    }
    if(h>=6&&h<12){g="Good Morning.&#128522";}
    else if(h>=12&&h<18){g="Good Afternoon..&#128512";}
    else{g="Good Evening...&#128564";}
    document.getElementById('time').innerHTML = h+":"+m+":"+s+"  "+dy+" "+d+" "+mo+" "+y+"<br>" + g;
  }
  function checkTime(i) {
    if (i < 10) {i = "0" + i};  // add zero in front of numbers < 10
    return i;
}